<template>
  <div id="course-order-form">
    <el-form :inline="true" :model="courses">
      <el-form-item label="课程类型">
        <el-select v-model="courses.type" placeholder="课程类型">
          <el-option :label="item.title" :value="item.id" v-for="item in courseType" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="支付时间">
        <el-date-picker
          v-model="courses.payTime"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
        <el-button type="default" @click="clear">清除筛选项</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 课程订单查询表单
 */
import { shortcuts } from './orderForm.config'
export default {
  name: 'courseOrderForm',
  data () {
    return {
      courses: {
        type: -1,
        payTime: ''
      },
      courseType: [],
      pickerOptions: {
        shortcuts
      }
    }
  },
  created () {
    this.fetchCourseType()
  },
  methods: {
    fetchCourseType () {
      this.courseType = [{ id: -1, title: '全部' }, { id: 1, title: '育婴课程' }, { id: 2, title: '育儿课程' }]
    },
    search () {
      this.$emit('searchOrders', this.courses)
    },
    clear () {
      this.courses = {
        type: -1,
        payTime: ''
      }
      this.$emit('clearSearchConditions', this.courses)
    }
  }
}
</script>



