<template>
  <div id="course-orders" class="course-orders-container"><el-row>
      <el-col>
        <el-card class="card-box pt-0" shadow="hover">
          <course-search-form
            @searchOrders="searchOrders" @clearSearchConditions="clearSearchConditions"></course-search-form>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-card class="card-box" shadow="hover">
          <el-row>
            <el-col><span class="count">数据统计：共{{total}}条</span></el-col>
          </el-row><br>
          <el-row>
            <el-col>
              <course-order-table :tableData="orders" :isPagination="isPagination"
                :page="currentPage" :total="total" :fields="fields"
                @changeCurrentPage="changeCurrentPage"></course-order-table>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 课程订单列表
 *  */
import CourseSearchForm from './CourseOrderForm'
import CourseOrderTable from './OrderTable'
import { courseOrdersFields } from './orderTable.config'
export default {
  name: 'courseOrders',
  data () {
    return {
      currentPage: 1,
      total: 0,
      searchParams: {},
      isPagination: true,
      fields: courseOrdersFields,
      orders: []
    }
  },
  components: {
    'course-search-form': CourseSearchForm,
    'course-order-table': CourseOrderTable
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['订单管理', '课程订单'])
  },
  methods: {
    searchOrders (params) {
      this.searchParams = params
      this.fetchOrders()
    },
    fetchOrders () {
    },
    changeCurrentPage (currentPage) {
      this.currentPage = currentPage
      this.fetchOrders()
    },
    clearSearchConditions (params) {
      this.searchParams = params
      this.fetchOrders()
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
</style>



